import React, { useState } from "react";
import axiosInstance from "../../api/axiosInstance";
import { useNavigate } from "react-router-dom";
import useUserInfo from "../../hooks/useUserInfo";
import "../../styles/DeleteAccount.css";

function DeleteAccount() {
  const navigate = useNavigate();
  const userInfo = useUserInfo();
  const [password, setPassword] = useState("");
  const [showPasswordInput, setShowPasswordInput] = useState(false);

  const handleDeleteClick = () => {
    setShowPasswordInput(true);
  };

  const handleChange = (e) => {
    setPassword(e.target.value);
  };

  const handleConfirmDelete = async () => {
    if (!password) {
      alert(" .");
      return;
    }

    try {
      await axiosInstance.delete("/auth/delete", {
        data: { id: userInfo.id, password },
      });

      //        
      localStorage.removeItem("MUSICOVERY_ACCESS_TOKEN");
      localStorage.removeItem("MUSICOVERY_REFRESH_TOKEN");
      localStorage.removeItem("MUSICOVERY_USER");

      alert("  .");
      navigate("/");
    } catch (error) {
      console.error("  :", error);
      alert("  .  .");
    }
  };

  return (
    <div className="mypage-section">
      <h2> </h2>
      <p> ?</p>

      {!showPasswordInput ? (
        <button className="delete-button" onClick={handleDeleteClick}>
           
        </button>
      ) : (
        <div className="password-confirm">
          <input
            type="password"
            placeholder=" "
            value={password}
            onChange={handleChange}
          />
          <button
            className="confirm-delete-button"
            onClick={handleConfirmDelete}
          >
             
          </button>
        </div>
      )}
    </div>
  );
}

export default DeleteAccount;
