import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import Header from "../components/common/Header";
import axios from "../api/axiosInstance";
import "../styles/Signup.css";

const SignupPage = () => {
  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [passwd, setPasswd] = useState("");
  const [nickname, setNickname] = useState("");
  const [phone, setPhone] = useState("");
  const [address, setAddress] = useState("");
  const [isEmailSent, setIsEmailSent] = useState(false);
  const [nicknameMessage, setNicknameMessage] = useState("");
  const [isNicknameValid, setIsNicknameValid] = useState(false);
  const [passwdMessage, setPasswdMessage] = useState("");
  const [isPasswdValid, setIsPasswdValid] = useState(false);

  //  : 2~20  , ,   ( )
  const nicknameRegex =
    /^[a-zA-Z-0-9!@#$%^&*()_+~`|}{[\]:;?><,./-=]{2,20}$/;
  //  :  8,    
  const passwdRegex =
    /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d!@#$%^&*()_+~`|}{[\]:;?><,./-=]{8,}$/;

  const handleSignup = async () => {
    if (!isEmailSent) {
      alert(
        "   .    ."
      );
      return;
    }

    if (!isNicknameValid || !isPasswdValid) {
      alert("   .  .");
      return;
    }

    const signupData = {
      email,
      passwd,
      nickname,
      phone,
      address,
    };

    try {
      await axios.post("/auth/signup", signupData);
      alert(" !");
      navigate("/login");
    } catch (error) {
      if (error.response && error.response.status === 409) {
        alert(error.response.data.message);
      } else {
        console.error(" :", error);
        alert(" .");
      }
    }
  };

  const handleEmailVerification = async () => {
    try {
      const signupData = { email };
      const response = await axios.post(
        "/auth/signup/verify-email",
        signupData
      );
      alert(response.data.message);
      setIsEmailSent(true);
    } catch (error) {
      if (error.response && error.response.status === 409) {
        alert(error.response.data.message);
      } else {
        console.error("  :", error);
        alert("    .");
      }
    }
  };

  const checkNickname = async (value) => {
    if (!nicknameRegex.test(value)) {
      setNicknameMessage(
        " 2~20  , ,  ."
      );
      setIsNicknameValid(false);
      return;
    }

    try {
      const response = await axios.get(`/auth/signup/check-nickname`, {
        params: { nickname: value },
      });
      setNicknameMessage(response.data.message);
      setIsNicknameValid(true);
    } catch (error) {
      if (error.response && error.response.status === 409) {
        setNicknameMessage(error.response.data.message);
        setIsNicknameValid(false);
      } else {
        console.error("   :", error);
        setNicknameMessage("   .");
        setIsNicknameValid(false);
      }
    }
  };

  const handleNicknameChange = (e) => {
    const value = e.target.value;
    setNickname(value);

    if (value.trim() !== "") {
      checkNickname(value);
    } else {
      setNicknameMessage("");
      setIsNicknameValid(false);
    }
  };

  const handlePasswdChange = (e) => {
    const value = e.target.value;
    setPasswd(value);

    if (!passwdRegex.test(value)) {
      setPasswdMessage(
        "  8,     ."
      );
      setIsPasswdValid(false);
    } else {
      setPasswdMessage("");
      setIsPasswdValid(true);
    }
  };

  return (
    <div>
      <Header />
      <div className="signup-container">
        <div className="signup-wrapper">
          <h2></h2>
          <div className="input-group">
            <input
              type="email"
              placeholder=""
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className="signup-input"
            />
            <button
              onClick={handleEmailVerification}
              disabled={!email}
              className="common-button"
            >
               
            </button>
          </div>
          <input
            type="password"
            placeholder=""
            value={passwd}
            onChange={handlePasswdChange}
            className="signup-input"
          />
          <span
            className={`signup-message ${isPasswdValid ? "valid" : "invalid"}`}
          >
            {passwdMessage}
          </span>
          <input
            type="text"
            placeholder=""
            value={nickname}
            onChange={handleNicknameChange}
            className="signup-input nickname-input"
          />
          <span
            className={`signup-message ${
              isNicknameValid ? "valid" : "invalid"
            }`}
          >
            {nicknameMessage}
          </span>
          <input
            type="text"
            placeholder=""
            value={phone}
            onChange={(e) => setPhone(e.target.value)}
            className="signup-input"
          />
          <input
            type="text"
            placeholder=""
            value={address}
            onChange={(e) => setAddress(e.target.value)}
            className="signup-input"
          />
          <button onClick={handleSignup} className="common-button">
            
          </button>
          <button onClick={() => navigate("/login")} className="common-button">
            
          </button>
        </div>
      </div>
    </div>
  );
};

export default SignupPage;
