import React, { useEffect } from "react";

function VerifyPage() {
  useEffect(() => {
    const timer = setTimeout(() => {
      window.close(); //   
    }, 5000);

    return () => clearTimeout(timer); //    
  }, []);

  return (
    <div style={{ textAlign: "center", marginTop: "50px" }}>
      <h1> !</h1>
      <p>5    .</p>
    </div>
  );
}

export default VerifyPage;
