import React, { useState, useEffect } from "react";
import axiosInstance from "../../api/axiosInstance"; // axios  
import "../../styles/InfoEdit.css";
import { useNavigate } from "react-router-dom";

function InfoEdit({ setActiveTab }) {
  const navigate = useNavigate();
  const [userInfo, setUserInfo] = useState(null);
  const [formData, setFormData] = useState({
    email: "",
    passwd: "",
    confirmPasswd: "",
    phone: "",
    address: "",
  });

  const [passwordMatch, setPasswordMatch] = useState(null); //   
  const [passwordValid, setPasswordValid] = useState(null); //   
  const passwordRegex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[A-Za-z\d\W]{8,}$/;

  useEffect(() => {
    const storedUser = JSON.parse(localStorage.getItem("MUSICOVERY_USER"));
    if (storedUser) {
      setUserInfo(storedUser);
      setFormData({
        email: storedUser.email,
        passwd: "",
        confirmPasswd: "",
        phone: storedUser.phone || "",
        address: storedUser.address || "",
      });
    }
  }, []);

  const handleChange = (e) => {
    const { name, value } = e.target;

    if (name === "passwd") {
      setPasswordValid(passwordRegex.test(value));
    }

    if (name === "confirmPasswd") {
      setPasswordMatch(value === formData.passwd);
    }

    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    //         
    if (formData.passwd && !formData.confirmPasswd) {
      alert("  .");
      return;
    }

    //        
    if (passwordMatch === false) {
      alert("  .");
      return;
    }

    //         
    if (formData.passwd && !passwordValid) {
      alert("   .");
      return;
    }

    const updatedData = {
      email: userInfo.email,
      passwd: formData.passwd.trim() !== "" ? formData.passwd : undefined,
      phone: formData.phone.trim() !== "" ? formData.phone : userInfo.phone,
      address:
        formData.address.trim() !== "" ? formData.address : userInfo.address,
    };

    try {
      const response = await axiosInstance.put(
        `/auth/update/${userInfo.id}`,
        updatedData
      );
      const updatedUser = response.data;

      localStorage.setItem("MUSICOVERY_USER", JSON.stringify(updatedUser));
      alert("  .");
      setActiveTab("home");
      navigate("/mypage");
    } catch (error) {
      console.error("    :", error);
      alert("  .");
    }
  };

  if (!userInfo) {
    return <p> ...</p>;
  }

  return (
    <div className="mypage-section">
      <h2> </h2>
      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <label htmlFor="email"></label>
          <input
            type="email"
            id="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            disabled
          />
        </div>

        <div className="form-group">
          <label htmlFor="passwd"></label>
          <input
            type="password"
            id="passwd"
            name="passwd"
            value={formData.passwd}
            onChange={handleChange}
            placeholder=" "
          />
          {formData.passwd && (
            <span
              style={{
                color: passwordValid ? "green" : "red",
                fontSize: "0.9rem",
              }}
            >
              {passwordValid
                ? "  ."
                : "  8 ,     ."}
            </span>
          )}
        </div>

        <div className="form-group">
          <label htmlFor="confirmPasswd"> </label>
          <input
            type="password"
            id="confirmPasswd"
            name="confirmPasswd"
            value={formData.confirmPasswd}
            onChange={handleChange}
            placeholder=" "
          />
          {formData.confirmPasswd && (
            <span
              style={{
                color: passwordMatch ? "green" : "red",
                fontSize: "0.9rem",
              }}
            >
              {passwordMatch
                ? " ."
                : "  ."}
            </span>
          )}
        </div>

        <div className="form-group">
          <label htmlFor="phone"></label>
          <input
            type="text"
            id="phone"
            name="phone"
            value={formData.phone}
            onChange={handleChange}
          />
        </div>

        <div className="form-group">
          <label htmlFor="address"></label>
          <input
            type="text"
            id="address"
            name="address"
            value={formData.address}
            onChange={handleChange}
          />
        </div>

        <button type="submit"></button>
      </form>
    </div>
  );
}

export default InfoEdit;
